html {height: 100%; width: 100%}
body {
  background-color: #151515;
  color: white;
  height: 100%;
  width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
  width: 100%
}
#main {height:100%; width: 100%; font-size: 1vw;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  background-color: #6bf;
  color: white;
  height: 10vh;
}
header h1 {margin: 0; padding-left: 3%; padding-top: 0; line-height: 10vh; font-size: 2vw}

#inputDiv{height: 85vh; display: inline-block; width: 40vw; overflow: hidden;}
#inputForm { margin-left: 5vw;}
#inputForm h1{
	margin: 5vh 0 0;
}
#inputForm hr {
	width: 60%;
	border: 1px solid white;
	text-align: left;
  margin: 0;
}
#inputForm label{
	display: block;
	margin: 3vh 0;
  font-size: 2vw;
}
#inputForm input{
	text-align: center;
	border: 0;
	border-radius: .5vw;
	width: 75%;
	height: 5vh;
  font-size: 1.5vw;
}
#width{padding: 3% 0;}
#height{padding: 3% 0;}
#inputForm #submit {
	background: #65b0d8;
	background: #6bd;
	color: white;
	margin-top: 5vh;
  font-weight: bold;
  font-size: 1.25vw;
  height: 10vh;
}
#output{
  display:inline-block;
  width: 50vw;
  height: 85vh;
  text-align: center;
}
#outputHeader {
  height: 10vh;
  text-align:left;
}
#outputHeader h1{
  margin: 5vh 0 0 0;
}

#outputHeader hr{
  margin: 0; border: 1px solid white; width: 60%;
}
#tableContainer {height:70vh; overflow-x: auto; overflow-y: auto;}
#tableContainer table {margin: auto; table-layout: auto; border-spacing: .5vh; width: 100%; height: 100%;}
#tableContainer table tbody tr td {
  background: #353535; border-radius: 2px; padding: 30px;
}
#tableContainer table tbody tr:last-child td:last-child {
  background: #282828;
}
#errorMsg {
  width: 75%;
  line-height: 3vw;
  text-align: center;
  border-radius: 1px;
  background: red;
}
@media (orientation:portrait){
  header {
    height: 5vh; text-align: center;
  }
  header h1 {line-height: 5vh; padding: 0}

  #inputDiv{height:45vh; display: block; width:100%; text-align: center;}
  #inputForm { margin: 0; text-align: center;}
  #inputForm h1{
  	margin: 0; padding-top: 2vh; font-size: 3vh;
  }
  #inputForm hr {
  	width: 60%;
    margin-left: 20%;
  	border: 1px solid white;
  }
  #inputForm label{
  	display: block;
  	margin: 2vh 0;
    font-size: 2.5vh;
  }
  #inputForm input{
  	text-align: center;
  	border: 0;
  	border-radius: .25vh;
  	width: 50%;
    height: 0;
    font-size: 2.5vh;
  }
  #width{padding: 3% 0;}
  #height{padding: 3% 0;}
  #inputForm #submit {
  	margin-top: 1.5vh;
    font-size: 1.5vh;
    height: 5vh;
  }
  #output{
    display: block;
    width: 100%;
    height: 50vh;
    text-align: center;
  }
  #outputHeader {
    height: 10%;
    text-align:center;
  }
  #outputHeader h1{
    margin: 0; font-size: 3vh;
  }
  #outputHeader hr{
    margin: 0; border: 1px solid white; width: 60%; margin-left: 20%;
  }
  #tableContainer {height:80%;}
  #tableContainer table{border-spacing: .5vw;}
  #errorMsg {
    width: 100%;
    line-height: 5vh;
    border-radius: 0;
    font-size: 1.5vh;
  }
}
